import React, { useState } from "react";
import './CreateBoardModal.scss'; 
import instance from "../../../../api/request";
import { validateBoardTitle


 } from "../../../../common/utils/validateUtils"; 
interface BoardData {
  id: number;
  title: string;
  custom?: {
    description: string;
  };
}

interface ModalProps{
  saveBoard: (title:string)=>void,
  toggleModal: ()=>void
}


const CreateBoardModal = ({saveBoard, toggleModal}:ModalProps) => {
  const [inputValue, setInputValue] = useState('')
  const [isCorrectValue, setIsCorrectValue] = useState(true);

  const closeModal = ()=>{
    toggleModal();
    setInputValue('');
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.target.value);
  };

  const handleInputSubmit = () => {
    let isCorrect = validateBoardTitle(inputValue);
    setIsCorrectValue(isCorrect);
    if(!isCorrect){
      return;
    }
    saveBoard(inputValue);
    setInputValue('');
    closeModal();
  };


  return (
    <div className="modal-overlay">
      <div className="modal">
        <h2> </h2>
        <label htmlFor="board"> </label>
        <input 
          type="text" 
          value={inputValue}
          name="board"
          onChange={handleInputChange} 
          placeholder=" " 
        />
        {!isCorrectValue && <p>          , , , ,   .</p>}
        <div className="modal-btns-container">
          <button onClick={closeModal} className="close-btn"></button>
          <button onClick={handleInputSubmit} className="submit-btn"></button>          
        </div>

      </div>
    </div>

  );
};

export default CreateBoardModal;
