import Swal from 'sweetalert2';

export const showSuccessMessage = (messageTitle:string, messageText:string)=>{
    Swal.fire(messageTitle, messageText, 'success');
}

export const showErrorMessage = (messageTitle:string, messageText:string)=>{
    Swal.fire({
        icon: 'error',
        title: messageTitle,
        text: messageText,
      });
}

const showErrorByStatus = (status:number)=>{
    switch (status) {
        case 400:
          showErrorMessage('!', ' .   .');
          break;
        case 404:
          showErrorMessage('!', '  .');
          break;
        case 500:
          showErrorMessage('!', '   .  .');
          break;
        case 503:
          showErrorMessage('!', '  .  .');
          break;
        default:
          showErrorMessage('!', '   .   .');
          break;
      }
}

export const handleError = (err:any)=>{
    console.error("Failed to fetch boards", err);
    // ,          -
    if (err.response) {
      const status = err.response.status;
      //   -
      showErrorByStatus(status);
    } else if (err.request) {
      //   ,     (,   )
      showErrorMessage('!', '    .  -.');
    } else {
      //     
      showErrorMessage('!', '  .   .');
    }
  }

  export const showMessageDelete = async (messageTitle: string) => {
    const result = await Swal.fire({
      title: messageTitle,
      text: '     !',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: ', !',
      cancelButtonText: '',
    });
  
    //  true,    
    return result.isConfirmed;
  };